@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media (max-width: 650px){
  html {
    font-size: 55%;
  }
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 1.8rem;
  background-color: #0b0c0e;
  color: #f5f5f5;
  height: 100vh;
}

@media (max-width: 650px){
  body {
    background-color: #fff;
  }
}

.App {
  background-color: #2a2e34;
  margin: 5rem auto;
  max-width: 65rem;
  width: 100%;
  min-height: 100vh;
  border-radius: 1rem;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, .2);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  padding-top: 5rem;
  padding-bottom: 3rem;
}

@media (max-width: 650px){
  .App {
    margin: 0 auto;
    box-shadow: none;
    border-radius: 0;
  }
}

.home {
  width: 80%;
  margin: 0 auto;
  padding: 0;
}


h1 {
  margin-bottom: 5rem;
  text-align: center;
}

h2 {
  margin-bottom: 3rem;
  font-size: 3.5rem;
  text-align: center;
}

.intro-text {
  margin-bottom: 5rem;
  text-align: center;
  line-height: 1.7;
  font-size: 2rem;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}

.form-control {
  width: 100%;
  margin-bottom: 2.5rem;
}

label {
  display: block;
  margin-bottom: 1rem;
}

input[type=radio] + label {
  display: inline-block;
  margin-left: .8rem;
  margin-right: 2rem;
}

input[type=text],
input:not([type=radio]),
input[type=email],
textarea {
  display: inline-block;
  border: none;
  padding: 1rem 3rem;
  width: 100%;
  background-color: #3b3f46;
  border-radius: 5px;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  transition: .2s;
}

input[type=text]:focus,
input:not([type=radio]):focus,
input[type=email]:focus,
textarea:focus {
  background-color: #1e2328;
  outline: none;
}

.error {
  color: yellow;
  margin-top: .8rem;
}

.btn,
.btn:link,
.btn:visited {
  border: none;
  color: #2a2e34;
  background-color: yellow;
  width: 80%;
  padding: 1.5rem 3rem;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.8rem;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

form .btn {
  width: 100%;
}

video, img {
  width: 100% !important;
} 

.btn.btn-main {
width: 100%;
}

.btn-retake {
  background-color: transparent;
  border: 2px solid yellow;
  color: yellow;
  margin-bottom: 2rem;
  margin-top: 2rem;

}

.logo {
  border-radius: 5px;
  display: block;
}

.image-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@supports (-webkit-appearance: none) or (-moz-appearance: none) {
  input[type=radio] {
    --active: #df7410;;
    --active-inner: #2a2e34;
    --focus: 2px rgba(39, 94, 254, .3);
    --border: #f5f5f5;
    --border-hover: #275EFE;
    --background: #f5f5f5;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 21px;
    outline: none;
    display: inline-block;
    vertical-align: top;
    position: relative;
    margin: 0;
    cursor: pointer;
    border: 1px solid var(--border);
    border: 1px solid var(--bc, var(--border));
    background: var(--background);
    background: var(--b, var(--background));
    transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
  }
  input[type=radio]:after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    position: absolute;
    transition: transform 0.3s ease, opacity 0.2s;
    transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
  }
  input[type=radio]:checked {
    --b: var(--active);
    --bc: var(--active);
    --d-o: .3s;
    --d-t: .6s;
    --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
  }
  input[type=radio]:hover:not(:checked):not(:disabled) {
    --bc: var(--border-hover);
  }
  input[type=radio]:focus {
    box-shadow: 0 0 0 var(--focus);
  }
  input[type=radio]:not(.switch) {
    width: 21px;
  }
  input[type=radio]:not(.switch):after {
    opacity: 0;
    opacity: var(--o, 0);
  }
  input[type=radio]:not(.switch):checked {
    --o: 1;
  }
  input[type=radio] + label {
    font-size: 14px;
    line-height: 21px;
    display: inline-block;
    vertical-align: top;
    cursor: pointer;
    margin-left: 4px;
  }

  input[type=radio] {
    border-radius: 50%;
  }
  input[type=radio]:after {
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: var(--active-inner);
    opacity: 0;
    transform: scale(0.7);
    transform: scale(var(--s, 0.7));
  }
  input[type=radio]:checked {
    --s: .5;
  }
}



